import "./App.css";
import React, { useState } from "react";
import JSONDATA from "./saved_links.json";
import { styled, alpha } from '@mui/material/styles';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
// import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import InputBase from '@mui/material/InputBase';
// import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import Item from "./Item";
import Switch from '@mui/material/Switch';
// import FormGroup from '@mui/material/FormGroup';
// import FormControlLabel from '@mui/material/FormControlLabel';
// import Bookmarked from "./Bookmarked";
// import {
//   BrowserRouter,
//   Routes,
//   Route,
// } from "react-router-dom";
import { Button } from "@mui/material";


const MaterialUISwitch = styled(Switch)(({ theme }) => ({
  width: 62,
  height: 34,
  padding: 7,
  '& .MuiSwitch-switchBase': {
    margin: 1,
    padding: 0,
    transform: 'translateX(6px)',
    '&.Mui-checked': {
      color: '#fff',
      transform: 'translateX(22px)',
      '& .MuiSwitch-thumb:before': {
        backgroundImage: `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="20" width="20" viewBox="0 0 20 20"><path fill="${encodeURIComponent(
          '#fff',
        )}" d="M4.2 2.5l-.7 1.8-1.8.7 1.8.7.7 1.8.6-1.8L6.7 5l-1.9-.7-.6-1.8zm15 8.3a6.7 6.7 0 11-6.6-6.6 5.8 5.8 0 006.6 6.6z"/></svg>')`,
      },
      '& + .MuiSwitch-track': {
        opacity: 1,
        backgroundColor: theme.palette.mode === 'dark' ? '#8796A5' : '#aab4be',
      },
    },
  },
  '& .MuiSwitch-thumb': {
    backgroundColor: theme.palette.mode === 'dark' ? '#003892' : '#001e3c',
    width: 32,
    height: 32,
    '&:before': {
      content: "''",
      position: 'absolute',
      width: '100%',
      height: '100%',
      left: 0,
      top: 0,
      backgroundRepeat: 'no-repeat',
      backgroundPosition: 'center',
      backgroundImage: `url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" height="20" width="20" viewBox="0 0 20 20"><path fill="${encodeURIComponent(
        '#fff',
      )}" d="M9.305 1.667V3.75h1.389V1.667h-1.39zm-4.707 1.95l-.982.982L5.09 6.072l.982-.982-1.473-1.473zm10.802 0L13.927 5.09l.982.982 1.473-1.473-.982-.982zM10 5.139a4.872 4.872 0 00-4.862 4.86A4.872 4.872 0 0010 14.862 4.872 4.872 0 0014.86 10 4.872 4.872 0 0010 5.139zm0 1.389A3.462 3.462 0 0113.471 10a3.462 3.462 0 01-3.473 3.472A3.462 3.462 0 016.527 10 3.462 3.462 0 0110 6.528zM1.665 9.305v1.39h2.083v-1.39H1.666zm14.583 0v1.39h2.084v-1.39h-2.084zM5.09 13.928L3.616 15.4l.982.982 1.473-1.473-.982-.982zm9.82 0l-.982.982 1.473 1.473.982-.982-1.473-1.473zM9.305 16.25v2.083h1.389V16.25h-1.39z"/></svg>')`,
    },
  },
  '& .MuiSwitch-track': {
    opacity: 1,
    backgroundColor: theme.palette.mode === 'dark' ? '#8796A5' : '#aab4be',
    borderRadius: 20 / 2,
  },
}));

const Search = styled('div')(({ theme }) => ({
  position: 'relative',
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  '&:hover': {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginLeft: 0,
  width: '100%',
  [theme.breakpoints.up('sm')]: {
    marginLeft: theme.spacing(1),
    width: 'auto',
  },
}));

const SearchIconWrapper = styled('div')(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'inherit',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: '12ch',
      '&:focus': {
        width: '20ch',
      },
    },
  },
}));



function App() {
  const [mode, setMode] = useState("dark");
  const [query, setQuery] = useState("");
  const [list,setList] = useState([])
  const [flag,setFlag] = useState(0);
  
  var num = 1;
  const toggleMode = () => {
    if (mode==="light") {
      setMode("dark")
      document.body.style.backgroundColor = 'white';
    }
    else {
      setMode("light")
      document.body.style.backgroundColor = '#302e2e';
    }
  }
  return (
    <>
         { (flag===0) &&

            <div>


              <Box sx={{ flexGrow: 1 }}>
                <AppBar position="static" >
                  <Toolbar>
                    <Typography
                      variant="h6"
                      noWrap
                      component="div"
                      sx={{ flexGrow: 1, display: { xs: 'none', sm: 'block' } }}
                    >
                      Search ENGINE
                    </Typography>

                   
                    <MaterialUISwitch onClick={toggleMode} sx={{ m: 1 }} defaultChecked />
                    <Typography
                      variant="h6"
                      noWrap
                      component="div"
                      sx={{ flexGrow: 1, display: { xs: 'none', sm: 'block' } }}
                    >
                      {(mode==="light") ? "Light Mode" : "Dark Mode"}
                    </Typography>


                    <Search>
                      <SearchIconWrapper>
                        <SearchIcon />
                      </SearchIconWrapper>
                      <StyledInputBase
                        placeholder="Search…"
                        inputProps={{ 'aria-label': 'search' }}
                        onChange={(event) => {
                          setQuery(event.target.value);
                        }}
                      />


                    </Search>
                  </Toolbar>
                </AppBar>
              </Box>
              <div style={{display:"flex" , alignItem:"center", justifyContent:"center"}}>

              <Button varient="outlined" onClick={()=>{setFlag(1)}}>Show Bookmarked list</Button>
              <Button varient="outlined" onClick={()=>{setFlag(0)}}>Empty Bookmark List</Button>
              </div>
              {/* {console.log(flag)} */}
              <div style={{ disply: "flex", flexDirection: "row" }}>


                {JSONDATA.links
                  .filter((val) => {
                    if (query==="") {
                      return "Hello"
                    } else if (val.toLowerCase().includes(query.toLowerCase())) {
                      return "Hello";
                    }
                  })
                  .map((val, key) => {
                    return (
                      <div style={{ display: "flex", flexDirection: "row" }}>
                        <Item val={val} key={key} num={num++} mode={mode} list={list} showbookmark={0} setList={setList}/>
                      </div>
                    );
                  })}
              </div>

            </div>
          


    }
    {(flag===1) &&

         <div style={{ margin: "0 0 0 0" }}>
          <Box sx={{ flexGrow: 1 }}>
                <AppBar position="static" >
                  <Toolbar>
                    <Typography
                      variant="h6"
                      noWrap
                      component="div"
                      sx={{ flexGrow: 1, display: { xs: 'none', sm: 'block' } }}
                    >
                      Search ENGINE
                    </Typography>

                   
                    <MaterialUISwitch onClick={toggleMode} sx={{ m: 1 }} defaultChecked />
                    <Typography
                      variant="h6"
                      noWrap
                      component="div"
                      sx={{ flexGrow: 1, display: { xs: 'none', sm: 'block' } }}
                    >
                      {(mode==="light") ? "Light Mode" : "Dark Mode"}
                    </Typography>


                    <Search>
                      <SearchIconWrapper>
                        <SearchIcon />
                      </SearchIconWrapper>
                      <StyledInputBase
                        placeholder="Search…"
                        inputProps={{ 'aria-label': 'search' }}
                        onChange={(event) => {
                          setQuery(event.target.value);
                        }}
                      />


                    </Search>
                  </Toolbar>
                </AppBar>
              </Box>
              
              <div style={{display:"flex" , alignItem:"center", justifyContent:"center"}}>

              <Button varient="outlined" onClick={()=>{setFlag(0)}}>Show original list</Button>
              </div>
         {list.map((val, key) => {
                    return (
                      <div style={{ display: "flex", flexDirection: "row" }}>
                        <Item val={val} key={key} num={num++} mode={mode} list={list} showbookmark={1} setList={setList}/>
                      </div>
                    );
                  })}
       </div>    
}
    </>

  );
}

export default App;